/* Imports */

/* This was installed via NPM first https://github.com/necolas/normalize.css/ */
@import "normalize.css";

/*  mobile:  425px / 26.5625em;
    tablet:  740px, / 46.250em;
    tablet (well when the design breaks):  850px, / 53.125em;
    desktop: 980px / 61.250em;
    wide:    1300px / 81.250em;
*/

@custom-media --phone-viewport (width <= 26.5625em);
@custom-media --tablet-viewport (width >= 26.5625em) and (width <= 53.125em);
@custom-media --desktop-viewport (width >= 53.125em) and (width <= 61.250em;);
@custom-media --wide-viewport (width >= 61.250em) and (width <= 81.250em;);

@lost gutter 16px;

/* Base grey colour */
$grey-skeleton: #343133;

body {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

::selection {
 color: #1F1F1F;
 background: #F5E405;
}

html, body {
  margin: 0;
  height: 100%;
  font-family: 'Barlow', sans-serif;
}

h1, p {
  margin: 0;
  padding: 0;
}


header {
  position: fixed;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  height: 58px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  transition: top 0.2s ease-in-out;

  @media (--phone-viewport) {
      position: absolute;
    }

  @media (--tablet-viewport) {
      position: absolute;
    }

  a {
    margin: 9px 0 0 10px;
    width: 40px;
    height: 40px;
    z-index: 5;
    position: relative;
    display: block;
    opacity: 0.7;
    transition: opacity .25s ease;

    &:hover {
      opacity: 1;
    }
  }

  span {
    position: absolute;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    padding-top: 19px;
    color: white;
    font-size: 1rem;
    letter-spacing: 20px;
    text-transform: uppercase; 
    font-weight: 500;
    text-align: center;

    @media (--phone-viewport) {
      letter-spacing: 4px;
      font-size: 0.875rem;
      padding-top: 20px;
    }

    @media (--tablet-viewport) {
      letter-spacing: 17px;
    }

  }

}

nav {
    position: fixed;
    top: 42%;
    left: 0;
    width: 58px;
    z-index: 2;
    height: 100%;

    @media (--phone-viewport) {
      display: none;
    }

    ol {
      list-style: none;
      text-align: center;
      margin: 0;
      padding: 0;
    }

    a {
      display: inline-block;
      margin: 4px 0 4px -4px;
      height: 22px;
      font-size: 0.875rem;
      color: #fff;
      border: 1px solid rgba(255,255,255,0.4);
      width: 22px;
      line-height: 22px;
      border-radius: 50%;
      position: relative;
      overflow: hidden;
      cursor: pointer;
      text-decoration: none;
      transition: border .25s ease;

      &#intro-anchor {
        text-decoration: none;
        border: none;
        text-align: center;
        margin: 0;
        width: 100%;
        text-transform: uppercase;

        &.active {
          background-color: transparent;
          color: #fff;
          border-radius: 0;
          font-weight: 700;
        }
      }

      &:hover {
        text-decoration: none;
        border: 1px solid rgba(255,255,255,1);
      }

      &.active {
        background-color: rgba(255,255,255,0.3);
        color: rgba(255,255,255,1);
        text-decoration: none;
        border: 1px solid rgba(255,255,255,1);
      }
    }

}

section {
  color: #EFEFEF;
}

.full-screen {
  height: 100vh;
  width: 100vw;

  @media (--phone-viewport) {
    height: 100%;
    width: 100%;
  }

  @media (--tablet-viewport) {
    height: 100%;
    width: 100%;
  }
}

@keyframes down-arrow-before {
    50% {
      transform: rotate(45deg) translate(70%, 70%);
    }
    100% {
      transform: rotate(45deg) translate(70%, 70%);
    }
  }

@keyframes down-arrow-after {
  50% {
    transform: rotate(45deg) translate(110%, 110%);
    opacity: 0;
  }
  51% {
    transform: rotate(45deg) translate(-130%, -130%);
  }
  100% {
    transform: rotate(45deg) translate(-70%, -70%);
    opacity: 1;
  }
}

.content-wrap {
  display: flex;
  flex-direction: row;
  min-height: 100vh;
  justify-content: center;
	align-items: center;

  @media (--phone-viewport) {
    display: block;
    height: 100%;
    padding: 0;
    margin: 0;
  }

  @media (--tablet-viewport) {
    display: block;
    height: 100%;
    padding: 0;
    margin: 0;
  }

  .scroll-wrap {
    position: absolute;
    bottom: 5%;
    right: 50%;
    z-index: 9999;

    @media (--phone-viewport) {
      display: none;
    }

    @media (--tablet-viewport) {
      display: none;
    }

    .scroll-down-dude:before,
    .scroll-down-dude:after {
      content: '';
      display: block;
      width: 12px;
      height: 12px;
      transform: rotate(45deg);
      border-bottom: 4px solid #fff;
      border-right: 4px solid #fff;
    }

    .scroll-down-dude:before {
      animation: down-arrow-before 2.6s cubic-bezier(0.77, 0, 0.175, 1) infinite;
    }

    .scroll-down-dude:after {
      animation: down-arrow-after 2.6s cubic-bezier(0.77, 0, 0.175, 1) infinite;
    }
  }

  aside {
    display: flex;
    align-items: center;
    width: 50%;
    height: 100vh;
    justify-content: left;
    position: relative;

    @media (--phone-viewport) {
     width: 100%;
     height: 350px;
    }

    @media (--tablet-viewport) {
     width: 100%;
     height: 500px;
    }

    h1 {
      font-size: 9rem;
      line-height: 1;
      text-transform: uppercase;
      transform: skewX(20deg) skewY(15deg) rotateY(45deg);
      transition: transform 1s ease, text-shadow 1s ease;
      text-shadow: -20px 20px 20px rgba(0, 0, 0, 0.6);

      @media (--phone-viewport) {
        font-size: 5rem;
        margin-bottom: 0;
        margin-top: 0.4em;
      }
      
      .end {
        filter: blur(4px);
        transition: filter 1s ease;
      }

      .second {
        filter: blur(2px);
        transition: filter 1s ease;
      }

      .third {
        filter: blur(1px);
        transition: filter 1s ease;
      }

      &:hover {
        transform: skewX(0deg) skewY(0deg) rotateY(0deg);
        text-shadow: -20px 20px 20px rgba(0, 0, 0, 0);

        .end, .second, .third  {
          filter: blur(0px);
        }
        
      }
      
    }
  }

  .intro-left {
    width: 50%;
    padding-left: 5%;

    @media (--phone-viewport) {
     width: 80%;
     height: 50%;
     padding-top: 7em;
     padding-left: 10%;
    }

    @media (--tablet-viewport) {
     height: 50%;
     width: 80%;
     padding-top: 10em;
     padding-bottom: 5em;
     padding-left: 15%;
    }
        
  }

  .intro-right {
    width: 35%;
    padding-top: 5%;

    @media (--phone-viewport) {
     width: 90%;
     padding-left: 5%;
     margin-bottom: 2em;
    }

    @media (--tablet-viewport) {
     width: 80%;
     padding-left: 10%;
     margin-bottom: 2em;
    }

    h2 {
      opacity: 0.6;
      font-weight: 500;
      font-size: 0.875rem;
      color: #fff;
      letter-spacing: 10px;
      font-style: normal;
      text-transform: uppercase;
      margin-top: 1em;

      &:first-child {
        margin-top: 0;
      }
    }

    ul {
      list-style-type: none;
      padding: 0;
      margin-bottom: 2em;
      margin-top: 0.5em;

      @media (--phone-viewport) {
        margin-bottom: -4px;
        padding-bottom: 1.5em;
      }
    }

    li {
      font-size: 1.125rem;
      margin-bottom: 0.2em;
      line-height: 1.5;
    }
  }

  .tv-wrap {
    position: absolute;
    width: 584px;
    height: 355px;
    top: 33%;
    right: 3%;

    @media (--phone-viewport) {
      display: block;
      width: 90%;
      padding-left: 5%;
      padding-bottom: 0;
      margin: 0;
    }

    @media (--tablet-viewport) {
      right: 11%;
      top: 25%;
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 580px;

      @media (--phone-viewport) {
        width: 358px;
        left: 8px;
      }
    }

    iframe {
      position: absolute;
      top: 5px;
      left: 10px;
      width: 548px;
      height: 309px;

      @media (--phone-viewport) {
        width: 339px;
        height: 191px;
        left: 14px;
        top: 3px;
      }
    }
  }

  article {
    width: 48%;
    padding-left: 2%;
    position: relative;
    padding-top: 0;

    @media (--phone-viewport) {
      display: block;
      width: 90%;
      padding-left: 5%;
      padding-bottom: 0;
      margin: 15% 0 15% 0;
    }

    @media (--tablet-viewport) {
      display: block;
      width: 90%;
      padding-top: 8%;
      padding-left: 10%;
      padding-bottom: 15%;
      margin: 0;
    }
     
     h1 {
      font-weight: 700;
      letter-spacing: -0.01em;
      font-size: 3rem;
      line-height: 1.5;
      margin: 0;
      padding: 0;
      position: relative;
      z-index: 2;

      @media (--phone-viewport) {
        line-height: 1.1;
        margin-bottom: 0.25em;
      }
    }

    h2 {
      font-weight: 400;
      font-style: italic;
      color: rgba(255, 255, 255, 0.60);
      letter-spacing: -0.01em;
      font-size: 2rem;
      line-height: 1.5;
      margin: -0.5em 0 0.5em 0;
      padding: 0;
      position: relative;
      z-index: 2;
    }

    p {
      font-size: 20px;
      line-height: 1.5;
      width: 80%;
      position: relative;
      z-index: 2;
      padding: 0 0 1em 0;
      margin: 0;

      a {
        color: #fff;
        text-decoration: none;
        border-bottom: 2px solid rgba(255, 255, 255, 0.5);
        transition: border 0.3s ease;
        padding-bottom: 2px;

        &:hover {
          border-bottom: 2px solid rgba(255, 255, 255, 1);
        }
      }

      @media (--phone-viewport) {
       width: 100%;
      }

      @media (--tablet-viewport) {
       width: 90%;
      }
    }


    span {
      font-size: 44.38rem;
      color: rgba(255, 255, 255, 0.15);
      position: absolute;
      top: -248px;
      left: 13%;
      z-index: 1;
      user-select: none;

      @media (--phone-viewport) {
       font-size: 25rem;
       top: -150px;
       left: 5%;
      }

      @media (--tablet-viewport) {
       font-size: 36rem;
       top: -120px;
       left: 25%;
      }
    }
 }

}

